.wrapper{
    background-color: white;
    min-height: 300px;
    max-width: 1310px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    h1{
        margin-bottom: 10px;
        color: #333333;
        font-size: 36px;
        font-weight: 500;
    }
    a{
        text-decoration: none;
    }
    span{
        margin-top: 4px;
        margin: 0 4px;
    }
}